.card {
    margin: 15px;
    flex: 0 1 31%;
    width: 300px;
    transition: all 0.3s ease;
}



.card:hover {
    transform: translateY(-10px);
}

.card-icons {
    flex-shrink: 0;
}

.card-icon {
    margin: 0 10px 0 10px;
}

.card-title {
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;
    display: flex;
    justify-content: space-between;
}

.card-text {
    font-family: 'Barlow', sans-serif;
}

.card-tech {
    display: flex;
    flex-wrap: wrap;
    margin-top: auto;
}

.card-tech > div {
    margin-right: 25px;
    font-family: 'Quicksand', sans-serif;
    font-weight: 500;
    font-size: 90%;
    color: #0a275c;
}