@import url(https://fonts.googleapis.com/css?family=Barlow|Quicksand&display=swap);

.page-background {
    height: 100vh;
    width: 100%;
    /* min-height: 400px; */
    display: table;
    background-size: cover;
    background-image: url(/website/static/media/min3.4cf5751d.jpg);
    background-attachment: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.head-1 {
    margin-top: -80px;
    font-family: 'Quicksand', sans-serif;
    color: black;
    font-size: 350%;
    opacity: 0;
    transition: all 0.7s ease;
    transform: translateY(-50px);
}

.head-1.fade {
    opacity: 1;
    transform: translateY(0px);
}

.head-2 {
    margin-top: 25px;
    font-family: 'Barlow', sans-serif;
    color: black;
    font-size: 120%;
    opacity: 0;
    transition: all 0.7s ease;
    transform: translateY(50px);
}

.head-2.fade {
    opacity: 1;
    transform: translateY(0px);
}

.arrow {
    /* margin-top: auto; */
    position: absolute;
    top: 85%;
    transition: all 0.3s ease;
}

.arrow:hover {
    transform: translateY(-10px);
}

.navbar {
    transition: background 250ms ease-in;
}

.nav-scrolled {
    background: #f4f4f4;    
    box-shadow: 0 3px 20px rgba(0,0,0,0.2);
}

span.navbar-brand {
    font-size: 150%;
    cursor: pointer;
    font-family: 'Barlow', sans-serif;
}

.nav-link {
    margin-left: 12px;
    /* font-family: 'Quicksand', sans-serif; */
    font-size: 125%;
    font-family: 'Barlow', sans-serif;
    cursor: pointer;
}

.nav-item > .active {
    border-bottom: 2px solid #333;
}

button {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
}




.about-container {
    min-height: 85vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about-title {
    margin: 35px 0 35px 0;
    font-family: 'Barlow', sans-serif;
    color: black;
    font-size: 250%;
}

.about-text {
    margin: 35px 0 35px 0;
    font-family: 'Barlow', sans-serif;
    /* font-family: 'Quicksand', sans-serif; */
    color: black;
    font-size: 120%;
    max-width: 700px;
    text-align: center;
}

.about-text > b{
    color: #0a275c;
}
.exp-container {
    height: 80vh;
    background: #abbaab;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ffffff, #abbaab); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.exp-flex {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.exp-title {
    margin: 60px 0 60px 0;
    font-family: 'Barlow', sans-serif;
    color: black;
    font-size: 180%;
}

.position {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.job {
    flex: 0 1 37%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    font-family: 'Barlow', sans-serif;
}

.company {
    flex: 1 0 50%;
    color: #0a275c;
    font-weight: 700;
    font-size: 150%;
}

.time {
    align-self: auto;
    font-style: italic;
    font-weight: 500;
}

.title {
    margin-bottom: 20px;
    flex: 1 0 100%;
    font-style: italic;
    font-weight: 700;
    font-size: 120%;
}

.description {
    flex: 1 0 100%;
    font-size: 120%;
}

.img-thumbnail {
    margin-left: 45px;
}

ul {
    list-style: none;
    font-family: 'Quicksand', sans-serif;
    font-size: 90%;
    margin: 20px 0 0 0;
    padding-left: 1.2em;
    text-indent: -1.2em;
    white-space: nowrap;
    -webkit-column-count: 2;
            column-count: 2;
}

li:before {
    content: "⇀";
    display: block;
    float: left;
    width: 1.2em;
    color: #0a275c;
}
.proj-container {
    min-height: 90vh;
    background: #1488CC;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #2B32B2, #1488CC); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


}

.projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Barlow', sans-serif;
}

.proj-title {
    margin: 60px 0 60px 0;
    font-family: 'Barlow', sans-serif;
    color: white;
    font-size: 180%;
}

.proj-text {
    color: white;
    margin-bottom: 20px;
}

.proj-cards {
    margin-bottom: 50px;
    display: flex;
    flex-wrap: wrap;
}
.card {
    margin: 15px;
    flex: 0 1 31%;
    width: 300px;
    transition: all 0.3s ease;
}



.card:hover {
    transform: translateY(-10px);
}

.card-icons {
    flex-shrink: 0;
}

.card-icon {
    margin: 0 10px 0 10px;
}

.card-title {
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;
    display: flex;
    justify-content: space-between;
}

.card-text {
    font-family: 'Barlow', sans-serif;
}

.card-tech {
    display: flex;
    flex-wrap: wrap;
    margin-top: auto;
}

.card-tech > div {
    margin-right: 25px;
    font-family: 'Quicksand', sans-serif;
    font-weight: 500;
    font-size: 90%;
    color: #0a275c;
}
